body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.stick-header-title {
  height:40px;
  width: 100%;
  text-align: center;
  font-size: 20px;
  background-color: grey;
  padding:10px;
  color: white;
}

.btn {
  display: inline-block;
  border: 1px solid black;
  background: #007bff;
  color: white;
  margin-right: 20px;
  cursor: pointer;
  padding: 5px 20px;
}
.btn-primary {
  background: #007bff;
}

.btn-secondary {
  background: #117a8b;
}

.directory-container{
  margin: 30px;
  margin-left: 50px;
}

.directory-list, .register-form, .add-subsciber, .notification{
  margin-top: 30px;
  margin-bottom: 30px;
}

.validataion-error{
  color:red;
}

.directory-list table td, table th {
  width: 100px;
}

.directory-list table th {
  text-align: left;
}

.label{
  min-width: 90px;
  margin-right: 20px;
  display: inline-block;
}

